import React from 'react'
import FloatingWhatsApp from 'react-floating-whatsapp'
import avatar from './capi.svg'

export const WhatsApp = () => {
  return (
    <div style={{ left: '20px' } }>
      <FloatingWhatsApp 
        statusMessage={ "Pilot Online"}
        accountName={ "Capi"}
        phoneNumber={ "50760232931" }
        avatar={ avatar }
        chatMessage={ "Estupendo! 🤝 ¿Cómo puedo Ayudarle?" }
        placeholder={ "Escribe tu mensaje aquí..." }
        styles={{ right: '55px' } }
      />
    </div>
  );
}

