import React, { useState } from 'react';
import {
  Card, CardContent, CardHeader,
  CardMedia, Grid, Typography, CardActions,
  IconButton, Paper, InputBase, InputAdornment
} from '@material-ui/core';

import { Props as RemaxProps } from '../index';
import { IRemax } from './index';
import { makeStyles } from '@material-ui/core/styles';
import parse from 'html-react-parser';
import FavoriteBorderOutlinedIcon from '@material-ui/icons/FavoriteBorderOutlined';
import SearchIcon from '@material-ui/icons/Search';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';

import { Box } from "@material-ui/core";
import usePagination from "./usePagination";
import Pagination from './Pagination';

interface Props extends RemaxProps {
  properties: IRemax[]
}

const useStyles = makeStyles((theme) => ({
  item: {
    maxWidth: 545,
  },
  pageSearch: {
    [theme.breakpoints.up('sm')]: {
      display: "flex"
    }
  },
  paper: {
    padding: '2px 4px',
    alignItems: 'center',
    [theme.breakpoints.up('md')]: {
      marginLeft: 15,
      width: '50%',
    }
  }
}))


function Remax(props: Props) {
  const classes = useStyles()
  // eslint-disable-next-line
  const { application: { borrower }, setApplication, properties } = props;

  // eslint-disable-next-line
  function onApplicationChange(key: string, value: string) {
    setApplication({
      borrower: {
        [key]: value
      },
    });
  }

  // eslint-disable-next-line
  function onWageChange(wage: string) {
    setApplication({ borrower: { wage: Number(wage) || undefined } });
  }

  // eslint-disable-next-line
  function onStartDateSelected(startDate?: number | null): void {
    if (startDate) {
      setApplication({
        borrower: { startDate },
      });
    }
  }

  const PER_PAGE = 2;
  const [searched, setSearched] = useState('')
  const [rows, setRows] = useState<IRemax[]>(properties);

  const count = Math.ceil(rows.length / PER_PAGE);
  const _DATA = usePagination(rows, PER_PAGE);

  const requestSearch = (ev: React.ChangeEvent<HTMLInputElement>) => {
    setSearched(ev.target.value)
    const filteredRows = properties.filter((row) => {
      return row.ListingTitle_es.toLowerCase().includes(searched.toLowerCase());
    });
    setRows(filteredRows);
  };

  const cancelSearch = () => {
    setSearched('')
    setRows(properties)
  }

  return (
    <Box p="5">
      <Box className={classes.pageSearch}>
        <Pagination
          currentPage={_DATA.currentPage}
          totalPages={count}
          handlePrevPage={_DATA.prev}
          handleNextPage={_DATA.next}
        />

        <Paper
          className={classes.paper}
        >
          <InputBase
            style={{ marginLeft: '5px', width: '80%' }}
            placeholder="Busqueda por Ubicación"
            onChange={requestSearch}
            value={searched}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  onClick={cancelSearch}
                  edge="end"
                >
                  {
                    searched ? <CloseOutlinedIcon /> : null
                  }
                </IconButton>
              </InputAdornment>
            }
          />
          <IconButton type="button" style={{ padding: '10px' }} aria-label="search">
            <SearchIcon />
          </IconButton>
        </Paper>
      </Box>

      <Grid container spacing={4} justify="center" style={{ marginTop: 3 }}>
        {
          _DATA.currentData().length > 0 ?
            (
              _DATA.currentData().map(p => {
                const image = p.Images.split('|')[0]
                const descr = parse(p.publicRemarks_es) //.slice(0,100).concat(" ..."))

                return (
                  <Grid key={p.ListingKey} item xs={12} sm={6} >
                    <Card className={classes.item}>
                      <CardHeader
                        action={
                          <IconButton aria-label="settings">
                            <FavoriteBorderOutlinedIcon />
                          </IconButton>
                        }
                        title={p.ListingTitle_es}
                        subheader={`${p.ListPrice} - ${p.ContractType_es}`}
                      />
                      <CardMedia
                        component="img"
                        height="194"
                        image={image}
                        alt="Ok"
                      />
                      <CardContent>
                        <Typography variant="body2" color="textSecondary">
                          {descr}
                        </Typography>
                      </CardContent>
                      <CardActions>

                      </CardActions>
                    </Card>
                  </Grid>

                )
              })
            ) : null
        }
      </Grid>
    </Box>
  )
}

export default Remax;