interface Props extends QuotationProps {
  laboralEntitys: LaboralEntity[];
  termsLoan: TermLoan[];
  sectors: LaboralSector[];
  tmarcas: IMarca[];
  tmodelos: IModelo[];
  ttarifas: ITarifa[];
  tproducts: IProduct[];
}

interface IDetPagos {
  cuota: number;
  desde: string;
  hasta: string;
  dias: number;
  letra: number;
  tasa: number;
  saldoAnt: number;
  pagoCap: number;
  intCal: number;
  pagoInt: number;
  intPend: number;
  saldoAct: number;
}

interface IDetalle {
  detPagos: IDetPagos[];
  mesesParaJubilarse: number;
}

const formatUSD = (numb: number) => {
    var str = numb.toString().split(".");
    str[0] = str[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    if(str.length > 1) {
      str[1] =  str[1].length === 1 ? str[1].concat('0') : str[1]
    }
    return str.join(".");
}


function Form(props: Props) {
  const { quotation, setQuotation, laboralEntitys, tmarcas, tmodelos, tproducts } = props;
  const classes = useStyles();
  const product = window.localStorage.getItem("product") || "1"
  const [marca, setMarca] = useState(quotation.loan.marca);
  const [modelos, setModelos] = useState<IModelo[]>([]);  
  const [checked, setChecked] = useState(false);
  const [gFinanciado, setGFinanciado] = useState(true);
  const [gNotarial, setGNotarial] = useState(0)
  const [pjeGCierre, setPjeGCierre] = useState(0)
  const [gCierre, setGCierre] = useState(0)
  const [factor, setFactor] = useState(1)
  const [plazoMax, setPlazoMax] = useState(0)

  let { wage = 0.00, available = 0.00, alloance = 0.00, perDiem = 0.00 } = quotation.borrower

  //  Status Laboral
  //  0	Temporal	
  //  1	Permanente	
  //  2	Serv. Profesional	

  const user_admin = window.localStorage.getItem("Admin")

  let { gender, birthDate } = quotation.borrower;
  let salario = wage + alloance + perDiem

  const today = new Date() ; //  Ver como traerla del servidor
  let birth = new Date();
  if (birthDate !== undefined) {
    birth = new Date(birthDate)
  }

  const [detPagos, setDetPagos] = useState<IDetPagos[]>([])
  const [montoSol, setMontoSol] = useState(0)
  const [montoARecibir, setMontoARecibir] = useState(0)
  const [montoFinal, setMontoFinal] = useState(0)
  const [plazoSol, setPlazoSol] = useState(0)
  const [plazoFinal, setPlazoFinal] = useState(0)
  const [cuotaMax, setCuotaMax] = useState(0)
  // const [cuotaSol, setCuotaSol] = useState(0)
  const [cuotaFinal, setCuotaFinal] = useState(0)
  const [tasaSol, setTasaSol] = useState(0)
  const [tasaSol2, setTasaSol2] = useState(0)
  const [tasaSol10Au, setTasaSol10Au] = useState(0)
  const [tasaSol20Au, setTasaSol20Au] = useState(0)
  const [ajustAbono, setAjustAbono] = useState(false)

  //-- LLRR cambiado para los calculos por cada entidad
  const laboralEntities = laboralEntitys.filter(
    le => le.ruta === "810" && le.id_profesion === 5 
    && le.salario_min <= salario)

  const tproduct = tproducts.filter( p => p.id === Number(product))

  const anios: number[] = []
  const hoyEs = new Date()

  let aaaa_actual = hoyEs.getFullYear()
  const _mm = hoyEs.getMonth() + 1
  if(_mm > 9) aaaa_actual += 1
  for(let i=0; i<10; i++) {
    anios.push(aaaa_actual)
    aaaa_actual -= 1
  }

  let dd = today.getDate()
  let mm = today.getMonth() + 1
  let d1 = dd<10?'0'+dd.toString():dd.toString()
  let m1 = mm<10?'0'+mm.toString():mm.toString()

  function handleCapacity(tasa: number, tasa2: number = 0) {

    tasa = tasa/100
    tasa2 = tasa2/100

    let yyyy = birth.getFullYear() + (gender==='male' ? 62 : 57)
    // Fecha de Jubilacion
    let fj = new Date(yyyy.toString()+'-'+m1.toString()+'-'+d1.toString())

    //***************************************************************/
    //***************************************************************/
    //** CALCULO STANDAR  **/
    //***************************************************************/
    //***************************************************************/

    let monto_sol = montoSol

    let abono_ini = product === '3' ? (quotation.loan?.abonoAuto || 0) : (quotation.loan?.abonoHipoteca || 0)
    // let letra = quotation.loan.monthlyFee || cuotaSol
    // setCuotaSol(letra)
    // setCuotaFinal(letra)
    let letra = cuotaMax
    let letra_bk = letra
    let ciclos = 1

    let wrkV1: IDetalle = calc_monto_ah(plazoSol, fj)
    const { detPagos: wrkV2 } = wrkV1
    setDetPagos(wrkV2)


    let saldo_act = 0
    let saldo_ant = -9999
    let int_pend = 0
    let logg = []

    const ajusteMonto = () => {
      for (let i=0; i<wrkV2.length; i++) {

        let cuota = wrkV2[i] 
        let f1 = new Date(cuota.hasta)
        let f2 = new Date(cuota.desde)
        let dias = (f1.getTime() - f2.getTime()) / (24*60*60*1000)
        if(saldo_ant === -9999) {
          saldo_ant = monto_sol
        }
        if(saldo_ant <= 0) {
          break;
        }
        let pago = letra
        pago -= int_pend
        let int_cal = 0
        let tasaActiva = 0
        if(i > 48 && tasa2 !== 0) {
          int_cal = Math.round(saldo_ant*tasa2*dias/365*100)/100
          // int_cal = Number((Math.round(saldo_ant*tasa2/12*100)/100).toFixed(2))
          tasaActiva = tasa2
        } else {
          int_cal = Math.round(saldo_ant*tasa*dias/365*100)/100
          // int_cal = Number((Math.round(saldo_ant*tasa/12*100)/100).toFixed(2))
          tasaActiva = tasa
        }
        pago -= Number(int_cal.toFixed(2))
        let tint_pend = Number((pago < 0 ? pago*(-1) : 0).toFixed(2))
        let pago_int = Number((pago > 0 ? int_cal : int_cal + pago).toFixed(2))
        saldo_ant = Number(saldo_ant.toFixed(2))
        saldo_act = Number((saldo_ant - (pago < 0 ? 0 : pago)).toFixed(2))

        logg.push({
          "cuota": i, 
          "saldo": saldo_act
        })

        wrkV2[i].dias = dias
        wrkV2[i].saldoAnt = Number(saldo_ant.toFixed(2))
        wrkV2[i].saldoAct = Number(saldo_act.toFixed(2))
        wrkV2[i].intCal = Number(int_cal.toFixed(2))
        wrkV2[i].pagoInt = Number(pago_int.toFixed(2))
        wrkV2[i].letra = Number(letra.toFixed(2))
        wrkV2[i].tasa = Number((Math.round(tasaActiva*100/12*1000000)/1000000).toFixed(6))
        if(pago > 0) {
          wrkV2[i].pagoCap =  Number(pago.toFixed(2))
          wrkV2[i].intPend = 0
        } else {
          wrkV2[i].pagoCap = 0
          wrkV2[i].intPend =  Number(tint_pend.toFixed(2))
        }

        if(saldo_act <= 0) {
          break;
        }

        saldo_ant = saldo_act
        int_pend = tint_pend
        if(saldo_ant < cuota.letra) {
          // eslint-disable-next-line @typescript-eslint/no-unused-vars
          let dias = 30.465
          if(i < wrkV2.length - 1) {
            let cuota = wrkV2[i+1] 
            let f1 = new Date(cuota.hasta)
            let f2 = new Date(cuota.desde)
            dias = (f1.getTime() - f2.getTime()) / (24*60*60*1000)
          }

          // if(i > mesesParaJubilarse && mesesParaJubilarse > 0 && tasa2 !== 0) {
          if(i > 48 && tasa2 !== 0) {
            letra = saldo_ant + Math.round(saldo_ant*tasa2*dias/365*100)/100
            // letra = Number((saldo_ant + Math.round(saldo_ant*tasa2/12*100)/100).toFixed(2))
          } else {
            letra = saldo_ant + Math.round(saldo_ant*tasa*dias/365*100)/100
            // letra = Number((saldo_ant + Math.round(saldo_ant*tasa/12*100)/100).toFixed(2))
          }
        }
        ciclos += 1
      }
      setDetPagos(wrkV2)
    }

    do {
      ciclos = 1
      saldo_ant = -9999
      int_pend = 0
      logg = []

      // if(saldo_act !== 0 && letra_bk > 0) {
      //   return (
      //     <div style={{
      //       display:'flex',
      //       height:'100%',
      //       justifyContent: 'center',
      //       alignItems: 'center'
      //     }}>
      //       <CircularProgress />
      //     </div>
      //   );
      // }

      ajusteMonto()
      if(ciclos >= 240) debugger
      if(saldo_act <= 0 && ciclos >= plazoSol) {
        setMontoFinal(monto_sol)
        setMontoSol(monto_sol)
        setCuotaFinal(letra_bk)
        setPlazoFinal(ciclos)
        setQuotation({
          loan: { 
            term: ciclos, bank: "810", amount: monto_sol, 
            paysYear: undefined, cashOnHand: monto_sol,
            monthlyFee: letra_bk, monthlyFee2: Number(wrkV2[wrkV2.length-1].letra.toFixed(2))
          }
        });
        if(product === "3") {
          setQuotation({ loan: { abonoAuto: abono_ini }});
        }
        if(product === "4") {
          setQuotation({ loan: { abonoHipoteca: abono_ini }});
        }
        break;
      }
      if(saldo_act > 0 && ciclos >= plazoSol && ajustAbono) {
        abono_ini += 0.05
        letra = letra_bk
        const valorAuto = product === '3' ? (quotation.loan.valorAuto || 0) : (quotation.loan.hipoteca || 0)
        const montoCalc = Math.round((valorAuto - abono_ini + gNotarial)/factor)
        setMontoSol(montoCalc)
        monto_sol = montoCalc
      }
      // && letra_bk > cuotaMax
      if(saldo_act > 0 && ciclos >= plazoSol  && !ajustAbono) {
        setAjustAbono(true)
        alert("Debe aumentar el mondo de su Abono Inicial.")
        setQuotation({ loan: { amount: -999 }});
        break;
      }

      if(saldo_act <= 0 && ciclos < plazoSol  && !ajustAbono) {
        letra_bk = Number((letra_bk - 0.05).toFixed(2))
        letra = letra_bk
        if(letra <= 581.20) debugger
        saldo_act = 1
        setCuotaFinal(letra)
        setQuotation({ loan: { monthlyFee: letra}});
      }
    } while (saldo_act !== 0 && letra_bk > 0) 

    setEspera(false)
  }

  const calc_monto_ah = (plazoCalc: number, fj: Date) => {
    let mesesParaJubilarse = 0

    const fcot = new Date()
    let yyyy = fcot.getFullYear()
    let mm = fcot.getMonth() //+ 1  // TODO *  revisar esto, debe empezar en el mes
    let dd = fcot.getDate()

    let dd2 = dd
    if(dd2 > 30 && ((mm+1) === 4 || (mm+1) === 6 || (mm+1) === 9 || (mm+1) === 11 )) dd = 30
    else if(dd2 > 27 && mm === 2) {dd = 29}
    else {dd = dd2}

    let fecIni = new Date(yyyy, mm, dd)

    dd2 = dd
    if(dd2 > 30 && ((mm+1) === 4 || (mm+1) === 6 || (mm+1) === 9 || (mm+1) === 11 )) dd = 30
    else if(dd2 > 27 && mm === 2) {dd = 29}
    else {dd = dd2}

    let ftemp = new Date(yyyy, mm, dd)
    let ffpay = ftemp 

    yyyy = ffpay.getFullYear()
    mm = ffpay.getMonth()
    dd = ffpay.getDate()

    dd2 = dd
    let wPlazo = plazoCalc

    let i = 0
    let detPagos: IDetPagos[] = []

    while (i < wPlazo) {

      if(dd2 > 30 && ((mm+1) === 4 || (mm+1) === 6 || (mm+1) === 9 || (mm+1) === 11 )) dd = 30
      else if(dd2 > 27 && mm === 2) {dd = 29}
      else {dd = dd2}
  
      mm += 1
      if(mm >= 12) { 
        yyyy+=1
        mm = 0
      }
      ftemp = new Date(yyyy, mm, dd)

      if (ftemp > fj && mesesParaJubilarse === 0) {
        mesesParaJubilarse = i
        // break 
      }

      i += 1

      let item = {
        "cuota": i, 
        "desde": fecIni.toJSON().slice(0,10),
        "hasta": ftemp.toJSON().slice(0,10),
        "letra": 0,
        "tasa": 0,
        "dias": 0,
        "saldoAnt": 0,
        "pagoCap": 0,
        "intCal": 0,
        "pagoInt": 0,
        "intPend": 0,
        "saldoAct": 0,
      }
      detPagos.push(item)
      fecIni = ftemp
    }

    return {
      detPagos,
      mesesParaJubilarse
    }
  }

  const calculosPlazo = () => {
    if(!quotation.loan.valorAuto && !quotation.loan.hipoteca) return
    setEspera(true)

    setCuotaFinal(0)
    setAjustAbono(false)
    handleCapacity(tasaSol, tasaSol2)
  }
  const ajustarAbono = () => {
    handleCapacity(tasaSol, tasaSol2)
    setAjustAbono(false)
  }

  const handleTermBlur = (plazo: string) => {
    if(Number(plazo) > plazoMax) {
      setPlazoSol(plazoMax)
    }
  }
  // const handleCuotaBlur = (cuotaSol: string) => {
  //   if(Number(cuotaSol) > cuotaMax) {
  //     setCuotaSol(cuotaMax)
  //   }
  // }
  const handleTermChange = (plazo: string) => {
    setPlazoSol(Number(plazo));
    setQuotation({ loan: { amount: undefined }});
  }
  // const handleCuotaChange = (cuota: string) => {
  //   // setCuotaSol(Number(cuota));
  //   setCuotaFinal(Number(cuota));
  //   setQuotation({ loan: { monthlyFee:  Number(cuota) || undefined, amount: undefined }})
  // }
  const handleTasaChange = (cuota: string) => {
    setTasaSol(Number(cuota));
    setQuotation({ loan: { amount: undefined }});
  }
  const handleTasaChange2 = (cuota: string) => {
    setTasaSol2(Number(cuota));
    setQuotation({ loan: { amount: undefined }});
  }

  useEffect(() => {
    const entity = laboralEntities[0]
    let { 
      plazo_max
    } = entity
    // , debt_capacity, discount_capacity, 
    // discount_capacity_mortgage, debt_capacity_mortgage 

    let tasa = laboralEntities[0].tasa
    let tasa2 = 0
    const { 
      tasa: prod_tasa=0, tasa2: prod_tasa2=0, plazo_max: prod_plazo_max=0, 
      edad_max_mujer=0, edad_max_hombre=0,
      gastos_notaria=0, factor=0, pje_gastos_cierre=0
    } = tproduct[0]
    if(prod_tasa > 0) tasa = Number(prod_tasa)
    if(prod_tasa2 > 0) tasa2 = Number(prod_tasa2)
    if(prod_plazo_max > 0) plazo_max = Number(prod_plazo_max)

    setGNotarial(Number(gastos_notaria))
    setPjeGCierre(Number(pje_gastos_cierre)/100)
    setFactor(factor > 0 ? Number(factor) : 1)

    // const cap_debt_available = cap_debt - mortage
    let cap_discto_available = available //cap_debt_available < cap_discto ? cap_debt_available : cap_discto
    cap_discto_available = Number(cap_discto_available.toFixed(2))
    setQuotation({ loan: { cap_discount: cap_discto_available }});


    // setCuotaSol(cap_discto_available)
    setCuotaMax(cap_discto_available)
    setTasaSol(tasa)
    setTasaSol2(tasa)
    setTasaSol10Au(tasa)
    setTasaSol20Au(tasa2)

    // Edad máxima ghasta la que se puede prestar
    const yyyy = birth.getFullYear() + (gender==='male' ? edad_max_hombre : edad_max_mujer)
    const f1 = moment((today.getFullYear()).toString()+m1.toString()+d1.toString())
    const f2 = moment(yyyy.toString()+m1.toString()+d1.toString())

    const dd = f2.diff(f1,'days')
    let plazo_age = Math.floor(dd/30.46153846)

    if(plazo_max > plazo_age) {
      plazo_max = plazo_age
    }
    setPlazoSol(plazo_max)
    setPlazoMax(plazo_max)

  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  // LLRR: 2022-01-20
  const typeLoan = () => {
    let name = "personal"
    if(product === "3") {
      name = "de automóvil"
    } else if(product === "4") {
      name = "hipotecario"
    }
    return name
  } 

  function onMarcaChange(event: React.ChangeEvent<{ value: unknown }>) {
    const marca = event.target.value as number
    setMarca(marca)
    setQuotation({
      loan: {
        marca: marca || undefined,
      }
    })
  }
  function onModeloChange(event: React.ChangeEvent<{ value: unknown }>) {
    const modelo = event.target.value as number
    setQuotation({
      loan: {
        modelo: modelo || undefined,
      }
    })
  } 

  const handleYearChange = (valor: React.ChangeEvent<{ value: unknown }>) => {
    setQuotation({
      loan: {
        yyyyModelo: Number(valor.target.value) || undefined,
      }
    })
  }
  const handleHipChange = (valor: string) => {
    setQuotation({
      loan: {
        hipoteca: Number(valor) || undefined,
        amount: undefined
      }
    })
  } 
  const handleAbonoHipChange = (valor: string) => {
    setQuotation({
      loan: {
        abonoHipoteca: Number(valor) || undefined,
        amount: undefined
      }
    })
  } 
  const handleValorChange = (valor: string) => {
    setQuotation({
      loan: {
        valorAuto: Number(valor) || undefined,
        amount: undefined
      }
    })
  } 
  const handleAbonoChange = (valor: string) => {
    setQuotation({
      loan: {
        abonoAuto: Number(valor) || undefined,
        amount: undefined
      }
    })
  } 

  useEffect(() => {
    const { valorAuto=0, abonoAuto=0 } = quotation.loan
    if(valorAuto > 0 && product === '3') {
      // montoFinal = Math.round((valorAuto - abonoAuto + 750)/0.985)
      // 750.00 de Gastos legales
      // 1.5% sobre el monto a financiar Gasto de cierre
      const MS = Math.round((valorAuto - abonoAuto + (gFinanciado ? gNotarial : 0)) / (1 - pjeGCierre)*100)/100
      const gastos_cierre = Number((MS - (valorAuto - abonoAuto + (gFinanciado ? gNotarial : 0))).toFixed(2))
      const MSR = Number((valorAuto - abonoAuto + (gFinanciado ? gastos_cierre + gNotarial : 0)).toFixed(2))
      const MAR = Number((valorAuto - abonoAuto - (gFinanciado ? gastos_cierre + gNotarial : 0)).toFixed(2))
      setGCierre(gastos_cierre)
      setMontoSol(MSR)
      setMontoARecibir(MAR)
      
      setTasaSol (tasaSol10Au)
      setTasaSol2(tasaSol10Au)
      if((valorAuto - abonoAuto)/valorAuto*100 <= 80) {
        setTasaSol (tasaSol20Au)
        setTasaSol2(tasaSol20Au)
      }
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[quotation.loan.valorAuto, quotation.loan.abonoAuto, gFinanciado])

  useEffect(() => {
    const { hipoteca=0, abonoHipoteca=0 } = quotation.loan
    if(hipoteca > 0 && product === '4') {
      // montoFinal = Math.round((hipoteca - abonoHipoteca + 750)/0.985)
      // 750.00 de Gastos legales
      // 1.5% sobre el monto a financiar Gasto de cierre
      const MS = Math.round((hipoteca - abonoHipoteca + (gFinanciado ? gNotarial : 0)) / (1 - pjeGCierre)*100)/100
      const gastos_cierre = Number((MS - (hipoteca - abonoHipoteca + (gFinanciado ? gNotarial : 0))).toFixed(2))
      const MSR = Number((hipoteca - abonoHipoteca + (gFinanciado ? gastos_cierre + gNotarial : 0)).toFixed(2))
      const MAR = Number((hipoteca - abonoHipoteca - (gFinanciado ? gastos_cierre + gNotarial : 0)).toFixed(2))
      setGCierre(gastos_cierre)
      setMontoSol(MSR)
      setMontoARecibir(MAR)
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[quotation.loan.hipoteca, quotation.loan.abonoHipoteca, gFinanciado])
  
  useEffect(() => {
    if(tmodelos !== undefined)
      setModelos(tmodelos.filter(item => item.id_marca === Number(marca)))
  },[marca, tmodelos])

  useEffect(()=>{
    setQuotation({ loan: { amount: undefined }});
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  
  const detallePagos = (detPagos: IDetPagos[]) => {

    return (
      <Grid container justify='center'>
        <h1>Plan de Pagos</h1>
        <TableContainer component={Paper}>
          <Table size="small" aria-label="a dense table">
            <TableHead>
              <TableRow>
                <TableCell align="center">Cuota</TableCell>
                <TableCell align="center">Desde</TableCell>
                <TableCell align="center">Hasta</TableCell>
                <TableCell align="right">Dias</TableCell>
                <TableCell align="right">Letra</TableCell>
                <TableCell align="right">Tasa</TableCell>
                <TableCell align="right">Saldo Ant</TableCell>
                <TableCell align="right">Abono Cap</TableCell>
                <TableCell align="right">Interes</TableCell>
                <TableCell align="right">Abono Int</TableCell>
                <TableCell align="right">Interes Pendt</TableCell>
                <TableCell align="right">Saldo Actual</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {detPagos.map(cuota => 
                {
                  return (
                    <>
                      {cuota.dias > 0 ? (
                        <TableRow>
                        <TableCell align="center">{cuota.cuota}</TableCell>
                        <TableCell align="center">{cuota.desde}</TableCell>
                        <TableCell align="center">{cuota.hasta}</TableCell>
                        <TableCell align="right">{cuota.dias}</TableCell>
                        <TableCell align="right">{cuota.letra.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.tasa.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.saldoAnt.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.pagoCap.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.intCal.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.pagoInt.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.intPend.toFixed(2)}</TableCell>
                        <TableCell align="right">{cuota.saldoAct.toFixed(2)}</TableCell>
                      </TableRow>
                      ) 
                      : null}
                    </>
                  )
                }
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>      
    )
  }

  const [espera, setEspera] = useState(false)

  const Esperar = () => {
    return (
      <div style={{
          position: 'relative',
          top: '250px',
          left: '150px',
          zIndex: 99,
          height:'50px',
        }}
      >
        {espera ? <CircularProgress /> : null }
      </div>
    )
  }


  return (
    <>
      {montoFinal > 0 
        ? (
          <Grid container justify='center'>
            <Grid item xs={12} sm={5} classes={{item: classes.content}}>
              {((quotation.loan.causasRechazo?.length || 0) > 0) ? (
              <>
                <div className={classes.starsBackground}>
                  <RemoveCircleOutlineIcon classes={{root: classes.icon2}} />
                </div>
                <Typography 
                  align='center' 
                  variant='h4' 
                  paragraph
                >
                  Razones del Rechazo
                </Typography>
                <List>
                  {quotation.loan.causasRechazo?.map((causa, idx) => (
                    <ListItem>
                      <>
                        <ListItemIcon>
                          <CheckBoxOutlinedIcon />
                        </ListItemIcon>
                        <ListItemText 
                          key={idx}
                          primary={causa.description}
                          primaryTypographyProps={{
                            color: 'primary'
                          }}
                        />
                      </>
                    </ListItem>
                  ))}
                </List>
              </>
            ):(
              <>
                <div className={classes.starsBackground}>
                  <CheckCircleOutlineOutlinedIcon classes={{root: classes.icon}} />
                </div>
                <Typography 
                  align='center' 
                  variant='h5' 
                  paragraph
                >
                  {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.TITLE}
                </Typography>
                <Typography
                  align='center' 
                  color='textSecondary'
                  classes={{root: classes.subtitle}}
                >
                  {STRINGS.QUOTATION.PERSONAL_LOAN.LOAN.CONTENT(formatUSD(montoFinal || 0), plazoFinal || 0, formatUSD(cuotaFinal || 0), typeLoan())}
                </Typography>
              </>
              )}
            </Grid>
          </Grid>
        ) : null
      }

      <Esperar />  

      <Grid container>
        { product === '3' ? (
          <>
            <Grid item container xs={12} sm={12} spacing={1}> 
              <Grid item xs={6}>
                <h3>Descripción del Auto</h3>
              </Grid>
              <Grid item xs={6} id='otros-datos-sm'>
                <h3>Otros Datos</h3>
              </Grid>
            </Grid>
            <Grid item container xs={12} sm={6} spacing={1}> 
              <Grid item xs={12} sm={6}>
                <TextField
                  select
                  label=" Marca "
                  variant="outlined"
                  fullWidth
                  value={quotation.loan.marca}
                  onChange={onMarcaChange}
                >
                  {tmarcas.map((item, idx) => (
                    <MenuItem key={idx} value={item.id} selected = {item.id === Number(quotation.loan.marca)}>
                      {item.name}
                    </MenuItem>
                  ))}
                </TextField>
              </Grid>

              <Grid item xs={12} sm={6}>
                <TextField
                  select
                  label=" Modelo "
                  variant="outlined"
                  fullWidth
                  value={quotation.loan.modelo}
                  onChange={onModeloChange}
                >
                  {modelos.map((item, idx) => (
                    <MenuItem key={idx} value={item.id} selected = {item.id === Number(quotation.loan.modelo)}>
                      {item.name}
                    </MenuItem>
                  ))}
                </TextField>
              </Grid>

              <Grid item xs={12} sm={6}>
                <TextField
                  select
                  fullWidth
                  variant="outlined"
                  label=" Año "
                  type='text'
                  value={quotation.loan.yyyyModelo}
                  onChange={handleYearChange}
                >
                  {anios.map((aaaa, idx) => <MenuItem key={idx} value={aaaa}>{aaaa}</MenuItem>)}
                </TextField>
              </Grid>

              <Grid item xs={12}>
                <h3>Valor del Auto</h3>
              </Grid>
              <Grid item xs={12}> 
                <NumberField
                  label=" Valor "
                  onChange={handleValorChange}
                  value={quotation.loan.valorAuto ? quotation.loan.valorAuto: ""}
                />
              </Grid>
              <Grid item xs={12}> 
                <NumberField
                  label=" Abono "
                  onChange={handleAbonoChange}
                  value={quotation.loan.abonoAuto ? quotation.loan.abonoAuto: ""}
                />
              </Grid>

              <Grid item xs={12}> 
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography style={{ color: 'green' }}>Monto a Recibir: <strong>${ formatUSD(montoARecibir) }</strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Saldo a Financiar: <strong>${ formatUSD(montoSol) }</strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Gastos Legales: <strong>${ formatUSD(gNotarial) }</strong></Typography>
                  <Typography>Gastos de Cierre: <strong>${ formatUSD(gCierre) }</strong></Typography>
                  <Divider />
                  <Typography style={{ marginTop: '10px' }}>Total: <strong>${ formatUSD(gNotarial + gCierre) }&nbsp;&nbsp;&nbsp;<span style={{ color: 'blue' }}>{gFinanciado ? 'Financiado' : 'Pagar en Caja'}</span></strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox checked={ gFinanciado } color="primary" onChange={ ()=>setGFinanciado(!gFinanciado) } />}
                  label="Deseo finananciar los gastos?"
                />
              </Grid>
            </Grid>
              
            <Grid item container xs={12} sm={6} spacing={1} style={{ marginLeft: '5px' }} id='otros-datos'> 
              <Grid item xs={12} id='otros-datos-xs'>
                <h3>Otros Datos</h3>
              </Grid>
              <Grid item xs={12}>
                <NumberField
                  label=" Plazo máximo en meses "
                  onChange={handleTermChange}
                  onBlur={handleTermBlur}
                  value={plazoSol ? plazoSol: ""}
                />
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Capacidad Máxima: <strong>${ formatUSD(cuotaMax) }</strong></Typography>
                </Box>
              </Grid>
              
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography style={{ color: 'green' }}>Letra Aproximada: <strong>${ formatUSD(cuotaFinal ? cuotaFinal: 0) }</strong></Typography>
                </Box>
              </Grid>
            </Grid>
          </>
        ): null } 
      </Grid>

      <Grid container>
        { product === '4' ? (
          <>
            <Grid item container xs={12} sm={6} spacing={1}> 
              <Grid item xs={12}> 
                <NumberField
                  label=" Valor de la Propiedad "
                  onChange={handleHipChange}
                  value={quotation.loan.hipoteca ? quotation.loan.hipoteca: ""}
                />
              </Grid>
              <Grid item xs={12}> 
                <NumberField
                  label=" Abono "
                  onChange={handleAbonoHipChange}
                  value={quotation.loan.abonoHipoteca ? quotation.loan.abonoHipoteca : ""}
                />
              </Grid>
              <Grid item xs={12}> 
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography style={{ color: 'green' }}>Monto a Recibir: <strong>${ formatUSD(montoARecibir) }</strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Saldo a Financiar: <strong>${ formatUSD(montoSol) }</strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Gastos Legales: <strong>${ formatUSD(gNotarial) }</strong></Typography>
                  <Typography>Gastos de Cierre: <strong>${ formatUSD(gCierre) }</strong></Typography>
                  <Divider />
                  <Typography style={{ marginTop: '10px' }}>Total: <strong>${ formatUSD(gNotarial + gCierre) }&nbsp;&nbsp;&nbsp;<span style={{ color: 'blue' }}>{gFinanciado ? 'Financiado' : 'Pagar en Caja'}</span></strong></Typography>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox checked={ gFinanciado } color="primary" onChange={ ()=>setGFinanciado(!gFinanciado) } />}
                  label="Deseo finananciar los gastos?"
                />
              </Grid>
            </Grid>
 
            <Grid item container xs={12} sm={6} spacing={1}> 
              <Grid item xs={12}>
                <NumberField
                  label=" Plazo máximo en meses "
                  onChange={handleTermChange}
                  onBlur={handleTermBlur}
                  value={plazoSol ? plazoSol: ""}
                />
              </Grid>
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                  <Typography>Capacidad Máxima: <strong>${ formatUSD(cuotaMax) }</strong></Typography>
                </Box>
              </Grid>
              
              <Grid item xs={12}>
                <Box maxWidth='300px' padding='10px' marginTop='5px' border='1px solid gray' borderRadius='5px'>
                      <Typography style={{ color: 'green' }}>Letra Aproximada: <strong>${ cuotaFinal ? cuotaFinal: "0" }</strong></Typography>
                    </Box>
              </Grid>
            </Grid>              
            </>
          ): null }
      </Grid>
      
      <Grid container xs={12} style={{ marginTop: '15px'}}>
        <Grid item xs={4} >
          <Button 
            color="primary" 
            variant="contained" 
            onClick={calculosPlazo}
            disabled={plazoSol < 11}
          >Calcular</Button>
        </Grid>

        <Grid item xs={4}>
          {
            detPagos.length > 0 &&
            <FormControlLabel
              control={<Checkbox checked={checked} color="primary" onChange={()=>setChecked(!checked)} />}
              label="Ver Plan de Pagos"
            />
          }
        </Grid>

        <Grid item xs={4}> 
          {
            (ajustAbono) && (
              <Button 
                color="primary" 
                variant="contained" 
                onClick={ajustarAbono}
              >Ajuste Automático</Button>
            )
          }
        </Grid>
      </Grid>

      { user_admin && 
        <Grid container style={{marginTop: '25px'}}>
          <Grid container item xs={12} md={6}>
            <Grid container item xs={12} md={6}>
              <Grid item xs={12} md={12}>
                <FormLabel>Tasa 1</FormLabel>
              </Grid>
              <Grid item xs={12} md={12}>
                <NumberField
                  onChange={handleTasaChange}
                  value={tasaSol ? tasaSol: ""}
                />
              </Grid>
            </Grid>
            <Grid container item xs={12} md={6}>
              <Grid item xs={12} md={12}>
                <FormLabel>Tasa 2</FormLabel>
              </Grid>
              <Grid item xs={12} md={12}>
                <NumberField
                  onChange={handleTasaChange2}
                  value={tasaSol2 ? tasaSol2: ""}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      }

      {/* MOSTRAR EL DETALLE DEL PLAN DE PAGOS */}
      { checked && detallePagos(detPagos) }
    </>
  );
};

export default Form