import React, { useEffect, useState } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Props } from '../index';
import Form from './Form';

export interface IRemax {
  ListingKey: string;
  PropertyTypeName_es: string;
  ContractType_es: string;
  ListingTitle_es: string;
  publicRemarks_es: string;
  ProbableUse: string;
  StateDevProv: string;
  Location: string;
  LotSizeArea: number;
  LotSizeUnits: string;
  ConstructionSizeLiving: number;
  ConstructionSizeUnits: string;
  ConstructionSizeTotal: number;
  UnparseAddress: string;
  ListPrice: number;
  CurrencyListPrice: string;
  Images: string;
}

function Remax(props: Props) {

  const [propiedades, setPropiedades] = useState<IRemax[]>([])

  useEffect(() => {
    fetch("https://api.remax-cca.com/api/CanalPanama/97930BF2-1F31-47A1-9826-FC9F492275FA?json=true")
    .then(response => response.json())
    .then(data => {
      setPropiedades(data.information.properties);
    })
  }, [])
  

  if (propiedades.length <= 0) { 

    return (
      <div style={{
        display:'flex',
        height:'100%',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <CircularProgress />
      </div>
    );
  }

  return (
    <Form 
      { ...props } 
      properties={ propiedades }
    />);
};

export default Remax;